<template>
  <div class="card shadow"
       :class="type === 'dark' ? 'bg-default': ''">
    <div class="card-header border-0"
         :class="type === 'dark' ? 'bg-transparent': ''">
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'dark' ? 'text-white': ''">
            {{title}}
          </h3>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table class="table align-items-center table-flush"
                  :class="type === 'dark' ? 'table-dark': ''"
                  :thead-classes="type === 'dark' ? 'thead-dark': 'thead-light'"
                  tbody-classes="list"
                  :data="tableData">
        <template slot="columns">
          <th>Name</th>
          <th>Email</th>
          <th>Phone Number</th>
          <th>Position</th>
          <th>Birth</th>
          <th></th>
        </template>

        <template slot-scope="{row}">
          <th scope="row">
            <div class="media align-items-center">
              <!-- <a href="#" class="avatar rounded-circle mr-3">
                <img alt="Image placeholder" :src="row.avatar">
              </a> -->
              <div class="media-body">
                <span class="name mb-0 text-sm">{{row.name}}</span>

              </div>
            </div>
          </th>
          <td class="budget">
            {{row.email}}
          </td>
          <td>
            {{row.phone_number}}
          </td>
          <td>
            {{row.position}}
          </td>

          <td>
            <div class="d-flex align-items-center">
              {{row.birth_place}}, {{row.birth_date}}
              <!-- <span class="completion mr-2">{{row.completion}}%</span>
              <div>
                <base-progress :type="row.statusType"
                               :show-percentage="false"
                               class="pt-0"
                               :value="row.completion"/>
              </div> -->
            </div>
          </td>

          <td class="text-right">
            <base-dropdown class="dropdown"
                           position="right">
              <a slot="title" class="btn btn-sm btn-icon-only text-light" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                <i class="fas fa-ellipsis-v"></i>
              </a>

              <template>
                <a class="dropdown-item" href="#">Action</a>
                <a class="dropdown-item" href="#">Another action</a>
                <a class="dropdown-item" href="#">Something else here</a>
              </template>
            </base-dropdown>
          </td>

        </template>

      </base-table>
    </div>

    <div class="card-footer d-flex justify-content-end"
         :class="type === 'dark' ? 'bg-transparent': ''">
      <base-pagination total = total ></base-pagination>
    </div>

  </div>
</template>
<script>
  import axios from 'axios'
  export default {
    name: 'projects-table',
    props: {
      type: {
        type: String
      },
      title: String
    },
    data() {
      return {
        tableData: null,
        total: null
      }
    },
    mounted () {
      const AuthStr = 'Bearer ' + localStorage.getItem('token');
      axios.get('http://35.187.240.162/api/employee/', {'headers' : {Authorization: AuthStr}})
      .then(resp => {
        this.tableData = resp.data.data.employee[0]
        this.total = resp.data.data.total
        // eslint-disable-next-line no-console
        console.log(this.tableData)
      })
      .catch((error) => {
        // eslint-disable-next-line no-console
        console.log(error)
      })
    }
  }
</script>
<style>
</style>
